export default function TextGenerator() {
  const [messages, setMessages] = useState<Array<{ role: 'user' | 'assistant'; content: string }>>([]);
  const [prompt, setPrompt] = useState('');
  const [isGenerating, setIsGenerating] = useState(false);
  const chatContainerRef = useRef<HTMLDivElement>(null);

  const handleGenerate = async () => {
    if (!prompt) return;
    setIsGenerating(true);
    try {
      const newMessages = [...messages, { role: 'user' as const, content: prompt }];
      setMessages(newMessages);
      setPrompt('');
      await new Promise(resolve => setTimeout(resolve, 2000));
      setMessages([
        ...newMessages,
        { role: 'assistant' as const, content: `Response to: "${prompt}"` },
      ]);
      setTimeout(() => {
        chatContainerRef.current?.scrollTo({
          top: chatContainerRef.current.scrollHeight,
          behavior: 'smooth',
        });
      }, 100);
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setIsGenerating(false);
    }
  };

  return (
    <div className="flex h-screen bg-white">
      {/* Sidebar */}
      <div className="w-64 border-r border-gray-200 flex flex-col">
        <div className="flex items-center justify-between p-4 border-b border-gray-200">
          <div className="flex items-center space-x-3">
            <h1 className="text-xl font-semibold">BLACKBOXAI</h1>
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
            </svg>
          </div>
          <button className="p-2 hover:bg-gray-100 rounded-lg">
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            </svg>
          </button>
        </div>
        <div className="flex-1 overflow-y-auto">
          <nav className="py-2">
            {quickLinks.map((link) => (
              <button
                key={link.id}
                className="w-full flex items-center space-x-3 px-4 py-2 text-[13px] text-gray-600 hover:bg-gray-50 transition-colors"
              >
                <svg 
                  className="w-4 h-4 shrink-0" 
                  fill="none" 
                  stroke="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d={link.icon} />
                </svg>
                <span className="truncate">{link.name}</span>
              </button>
            ))}
          </nav>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col">
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b border-gray-200">
          <div className="flex items-center space-x-3">
            <button className="p-2 hover:bg-gray-100 rounded-lg">
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
            <button className="px-4 py-1.5 text-sm bg-black text-white rounded-full hover:bg-gray-800">
              + New
            </button>
          </div>
          <div className="flex items-center space-x-3">
            <button className="px-4 py-1.5 text-sm bg-black text-white rounded-full hover:bg-gray-800">
              Share
            </button>
            <div className="w-7 h-7 bg-blue-600 text-white rounded-full flex items-center justify-center text-sm font-medium">
              L
            </div>
          </div>
        </div>

        {/* Chat Area */}
        <div className="flex-1 overflow-hidden">
          <div ref={chatContainerRef} className="h-full overflow-y-auto p-4 space-y-6">
            {messages.map((message, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
              >
                <div className={`max-w-2xl p-4 rounded-lg ${
                  message.role === 'user' ? 'bg-black text-white' : 'bg-gray-100'
                }`}>
                  <p className="whitespace-pre-wrap">{message.content}</p>
                </div>
              </motion.div>
            ))}
          </div>
        </div>

        {/* Input Area */}
        <div className="border-t border-gray-200 p-4">
          <div className="max-w-4xl mx-auto">
            <div className="bg-white rounded-xl border border-gray-200">
              <div className="flex flex-col">
                <Textarea
                  value={prompt}
                  onChange={(e) => setPrompt(e.target.value)}
                  placeholder="Message Blackbox..."
                  className="min-h-[44px] max-h-[200px] resize-none border-0 focus:ring-0 p-3 text-gray-800 placeholder:text-gray-500"
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !e.shiftKey) {
                      e.preventDefault();
                      handleGenerate();
                    }
                  }}
                />
                <div className="flex items-center justify-between p-3 border-t border-gray-200">
                  <div className="flex items-center justify-between w-full">
                    <div className="flex items-center gap-0.5">
                      {actions.map((action) => (
                        <button
                          key={action.id}
                          className="px-2 py-1.5 text-gray-600 hover:bg-gray-50 rounded-lg flex items-center gap-1.5 whitespace-nowrap transition-colors text-[13px]"
                        >
                          <svg 
                            className="w-4 h-4" 
                            fill="none" 
                            stroke="currentColor" 
                            viewBox="0 0 24 24"
                          >
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d={action.icon} />
                          </svg>
                          <span>{action.name}</span>
                        </button>
                      ))}
                    </div>
                    <div className="flex items-center gap-2 pl-2">
                      <button className="p-1.5 text-gray-600 hover:bg-gray-50 rounded-lg transition-colors">
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z" />
                        </svg>
                      </button>
                      <button className="p-1.5 text-gray-600 hover:bg-gray-50 rounded-lg transition-colors">
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                        </svg>
                      </button>
                      <Button
                        onClick={handleGenerate}
                        disabled={isGenerating || !prompt}
                        className="bg-black text-white hover:bg-gray-800 px-4 py-1.5 h-8 text-[13px]"
                      >
                        {isGenerating ? 'Generating...' : 'Send'}
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <p className="text-xs text-center text-gray-500 mt-4">
              By using Blackbox you agree to the Terms and Privacy.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
